.navMenu {
  padding-inline-start: 0px;
  list-style: none;
  text-align: center;
  display: block;
  h2 {
    margin-top: 20px;
    margin-bottom: 30px;
    font-family: $font-2;
    color: $color-2;
  }
}
.nav-text {
  text-align: center;
  font-size: 200%;

  span {
    vertical-align: middle;
  }
  a {
    color: $color-3;
    border-radius: $borderRadius;
    text-decoration: none;
    font-family: $font-1;
    display: inline-block;

    &:after {
      content: "";
      display: block;
      height: 0.1em;
      width: 0%;
      margin-bottom: 9px;
      background-color: #000;
      transition: width 0.4s;
    }

    &:hover:after {
      width: 100%;
    }
    &:hover {
      color: $color-2;
    }
    &.active {
      color: $color-2;
      &:after {
        content: "";
        display: block;
        height: 0.1em;
        width: 100%;
        margin-bottom: 9px;
        background-color: #000;
      }
    }
  }
}

.sideBar {
  background-color: $color-1;
  height: 550px;
  min-height: 270px;
  width: 30%;
  border-radius: $borderRadius;
  margin-right: 10px;
}

@media (max-width: 780px) {
  .sideBar {
    background-color: $color-1;
    height: 80px;
    min-height: 80px;
    width: 100%;
    border-radius: 0px;
    margin-right: 0px;
    margin-bottom: 10px;
  }
  .navMenu {
    display: flex;
    h2 {
      margin-left: 5px;
      margin-top: 20px;
      margin-bottom: 30px;
      font-size: 120%;
      margin-right: 20px;
    }
  }
  .nav-text {
    font-size: 100%;
    align-self: center;
    margin-right: 20px;
    .itemTitle
    {
      white-space: nowrap;
    }
  }
}
@media (max-width:630px) {
  
  .itemTitle
  {
   display: none;
  }
  .nav-text
  {
    display: flex;
    margin-right: auto;
  }
  .icon
  {
    font-size: 5vw;
  }
  .navMenu {
    h2 {
      font-size: 4vw;
    }
  }
  .sideBar
  {
    min-height: 60px;
    height: 60px;
  }
}
@media (max-width:450px) {
  .sideBar
  {
    min-height: 50px;
    height: 50px;
  }
}