.Home
{
    width: 100%;
    height: 100%;
    background:linear-gradient(
        rgba(0, 0, 0, 0.8), 
        rgba(0, 0, 0, 0.7)
      ),url(../../assets/luta.jpg) ;
    background-repeat: no-repeat;
    background-size: 100% 100%;
    h2
    {
        text-align: center;
        color: $color-4;
        padding-top: 6em;  
        
    }
}
@media (max-width:520px) {
    .Home
    {
    height: 400px;
       h2
        {
            margin-bottom: 0px;
        }

    }
}
@media (max-width:350px) {
    .Home
    {
    height: 250px;
    }
}