body
{
    margin: 10px;
    font-family: $font-1;
    color : $color-3
}
.App
{
    display: flex;
    justify-content: center;
}
.pagePart
{
    width: 69%;
    max-width: 700px;
    background-color: $color-1;
    border-radius: $borderRadius;
    height: 550px;
    overflow: hidden;
}
@media  (max-width:780px) {
    .App
    {
        flex-direction: column;
    }
    .pagePart
    {
        width: 100%;
        max-width: 100%;
        border-radius: 0px;
    }
    body
    {
        margin: 0px;
    }
    
}
@media (max-width:520px) {
    .pagePart
    {
        height: fit-content;
       p
       {
           font-size: 3vw;
       }
       h2
       {
           font-size: 5vw;
       }
       h3 
       {
           font-size: 4vw;
       }
    }
}